export function getUser () {
  return localStorage.getItem('my-username');
}
export function getRole () {
  return localStorage.getItem('my-role');
}
export function getToken () {
  return localStorage.getItem('my-token');
}

export function setUser (username) {
  return localStorage.setItem('my-username', username);
}
export function setRole (role) {
  return localStorage.setItem('my-role', role);
}
export function setToken (token) {
  return localStorage.setItem('my-token', token);
}



