<template>
  <div class="canvas-board"
    ref="board"
    @mousemove="onDrag"
    @mouseleave="isDrag = false"
    @contextmenu.prevent>
    <div class="canvas-wrap"
      :style="backgroundStyle"
      @mousedown.prevent.middle="startDrag"
      @mouseup="isDrag = false"
      @mousewheel.prevent="onScroll">
      <slot :scale="scale" />
    </div>
  </div>
</template>

<script>
export default {
  name: "CanvasBoard",
  props: {
    imgURL: String,
    imgWidth: Number,
    imgHeight: Number
  },
  data: function() {
    return {
      isDrag: false,
      left: 0,
      top: 0,
      oldLeft: 0,
      oldTop: 0,
      mouseX: 0,
      mouseY: 0,
      fitScale: 1,
      scale: 1
    };
  },
  computed: {
    backgroundStyle: function() {
      return {
        left: this.left + "px",
        top: this.top + "px",
        width: this.width + "px",
        height: this.height + "px",
        backgroundImage: "url(" + this.imgURL + ")",
        backgroundSize: "100%"
      };
    },
    width: function() {
      return this.imgWidth * this.scale;
    },
    height: function() {
      return this.imgHeight * this.scale;
    },
    isLoaded: function() {
      return this.imgWidth > 0 && this.imgHeight > 0;
    }
  },
  watch: {
    isLoaded: function(val) {
      if (val) {
        this.setToFit();
      }
    },
    imgURL: function() {
      this.setToFit();
    }
  },
  mounted: function() {
    this.setToFit();
  },
  methods: {
    setToFit: function() {
      console.log("set to fit");
      var boardWidth = this.$refs.board.clientWidth;
      var boardHeight = this.$refs.board.clientHeight;
      var widthScale = this.imgWidth / boardWidth;
      var heightScale = this.imgHeight / boardHeight;
      if (widthScale > heightScale) {
        this.fitScale = boardWidth / this.imgWidth;
      } else {
        this.fitScale = boardHeight / this.imgHeight;
      }
      if (this.fitScale > 2) {
        this.fitScale = 2;
      }
      this.scale = this.fitScale;
      this.left = (boardWidth - this.width) / 2;
      this.top = (boardHeight - this.height) / 2;
    },
    startDrag: function(e) {
      this.oldLeft = this.left;
      this.oldTop = this.top;
      this.mouseX = e.clientX;
      this.mouseY = e.clientY;
      this.isDrag = true;
    },
    onDrag: function(e) {
      if (this.isDrag) {
        this.left = this.oldLeft + e.clientX - this.mouseX;
        this.top = this.oldTop + e.clientY - this.mouseY;
      }
    },
    onScroll: function(e) {
      var down = e.deltaY > 0;
      var delta = -0.1;
      var minScale = Math.min(0.4 * this.fitScale, 0.4);
      var maxScale = 16;
      if (!down) {
        delta = -delta;
      }
      var newScale = this.scale * (1 + delta);
      if (newScale < minScale || newScale > maxScale) {
        return;
      }
      if (!this.isDrag) {
        this.scale = newScale;
        this.left = this.left - e.offsetX * delta;
        this.top = this.top - e.offsetY * delta;
      }
    }
  }
};
</script>

<style scoped>
.canvas-board {
  position: relative;
  border: 2px solid #000;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.canvas-wrap {
  position: relative;
  border: 1px solid #000;
  display: inline-block;
}
.canvas-wrap > * {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
</style>



