import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/view/login/Login'
import Signup from '@/view/signup/Signup'
import Layout from '@/view/layout/Layout'
import TaskPanel from '@/view/tasks/TaskPanel'
import TaskNoteContainer from '@/view/tasks/TaskNoteContainer'
import TaskDetail from '@/view/tasks/TaskDetail'
import CreateTaskPanel from '@/view/taskcreation/CreateTaskPanel'
import CreateTaskName from '@/view/taskcreation/CreateTaskName'
import CreateTaskImgPackage from '@/view/taskcreation/CreateTaskImgPackage'
import CreateTaskDetails from '@/view/taskcreation/CreateTaskDetails'
import MarkArea from '@/view/marks/MarkArea'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/login',
      component: Login,
    },
    {
      path: '/signup',
      component: Signup,
    },
    {
      path: '/blank',
      component: {
        template: '<div>Loading...</div>',
        mounted () {
          this.$router.push("/");
        }
      }
    },
    {
      path: '/',
      component: Layout,
      children: [
        {
          path: '/',
          component: {
            template: `<h1>index</h1>`
          },
        },
        {
          path: 'tasks',
          component: TaskPanel,
          children: [
            {
              path: 'alltasks',
              component: TaskNoteContainer
            },
            {
              path: 'mytasks/:role/:username',
              component: TaskNoteContainer,
              props: true
            },
            {
              path: ':taskname',
              component: TaskDetail,
              props: true
            },
          ]
        },
        {
          path: 'createtask',
          component: CreateTaskPanel,
          children: [
            {
              path: 'taskname',
              component: CreateTaskName
            },
            {
              path: ':taskname/imgPackage',
              component: CreateTaskImgPackage,
              props: true
            },
            {
              path: ':taskname/taskdetails',
              component: CreateTaskDetails,
              props: true
            },
          ]
        },

        {
          path: '/mark/:taskname',
          component: MarkArea,
          props: true
        },
        {
          path: '/mark/:taskname/:imgID',
          component: MarkArea,
          props: true
        },
      ]
    },
    {
      path: '*',
      component: { template: `<h1>404</h1>` },
      hidden: true
    },
  ]
})



