<template>
  <div id="header"
    class="clearfix">
    <NavigationBar/>
    <UserBar/>
  </div>
</template>

<script>
import NavigationBar from "./HeaderNavigationBar";
import UserBar from "./HeaderUserBar";

export default {
  name: "Header",
  components: {
    NavigationBar,
    UserBar
  }
};
</script>


