<template>
  <div class="container">
    <h1>注册账号</h1>
    <BaseInput v-model="username"
      tips="请输入2-20位用户名，只能使用字母、数字和下划线"
      :rule="{minlength:2,maxlength:20,match:/^[a-zA-z0-9_]+$/}">
      用户名
    </BaseInput>
    <BaseInput v-model="password"
      type="password"
      tips="请输入6-40位密码"
      :rule="{minlength:6,maxlength:40}">
      密码
    </BaseInput>
    <BaseInput v-model="email"
      tips="请输入邮箱"
      :rule="{match:/^[a-zA-z0-9_-]+@[a-zA-z0-9_-]+(\.[a-zA-z0-9_-]+)+$/}">
      邮箱
    </BaseInput>
    <div class="role-wrap">
      <span>
        <input type="radio"
          name="role"
          id="worker"
          value="worker"
          v-model="role">
        <label for="worker">worker</label>
      </span>
      <span>
        <input type="radio"
          name="role"
          id="requester"
          value="requester"
          v-model="role">
        <label for="requester">requester</label>
      </span>
    </div>
    <button @click='signup'>注册</button>
  </div>
</template>

<script>
import BaseInput from "@/components/BaseInput";
import { signUp } from "@/api/base";
export default {
  name: "Signup",
  components: {
    BaseInput
  },
  data: function() {
    return {
      username: "",
      password: "",
      email: "",
      role: "worker",
      valid: true
    };
  },
  methods: {
    signup: function() {
      this.validate();
      if (this.valid) {
        signUp(this.username, this.password, this.email, this.role).then(
          response => {
            this.$router.push("/login");
          }
        );
      } else {
        alert("输入信息有误！");
      }
    },
    validate: function() {
      var inputs = this.$children;
      var index = 0;
      this.valid = true;
      while (index < inputs.length) {
        this.valid = inputs[index].validate() && this.valid;
        index++;
      }
    }
  }
};
</script>

<style scoped>
h1,
.base-input,
.role-wrap,
button {
  display: block;
  max-width: 350px;
}
.role-wrap {
  text-align: center;
}
.role-wrap > * {
  display: inline-block;
}
</style>



