<template>
  <div>
    <router-link to="/tasks/alltasks">全部任务</router-link>
    <router-link :to="'/tasks/mytasks/requester/' + getUser()"
      v-if="'requester' == getRole()">我发布的任务</router-link>
    <router-link :to="'/tasks/mytasks/worker/' + getUser()"
      v-if="'worker' == getRole()">我接受的任务</router-link>
    <button v-if="'requester' == getRole()"
      @click="$router.push('/createtask/taskname')">创建任务</button>
    <router-view></router-view>
  </div>
</template>

<script>
import { getUser, getRole } from "@/utils/localstorageinfo";
export default {
  name: "TaskPanel",
  methods: {
    getRole: getRole,
    getUser: getUser
  }
};
</script>



