import request from '@/request'
import { getUser } from '@/utils/localstorageinfo'

export function addTask (data) {
  return request({
    url: '/service/taskManagement/requester/add/' + getUser(),
    method: 'POST',
    data,
  })
}

export function getReleasedTasks () {
  return request({
    url: '/service/taskManagement/requester/display/releasedTask/' + getUser(),
    method: 'GET',
  })
}

export function modifyTaskInfo (data) {
  return request({
    url: '/service/taskManagement/requester/modify/' + getUser(),
    method: 'POST',
    data,
  })
}

export function deleteTask (taskname) {
  return request({
    url: '/service/taskManagement/requester/delete/' + getUser(),
    method: 'POST',
    data: {
      taskname
    }
  })
}

export function getReceiptTasks () {
  return request({
    url: '/service/taskManagement/worker/display/receiptTask/' + getUser(),
    method: 'GET',
  })
}

export function receiptTask (taskname) {
  return request({
    url: '/service/taskManagement/worker/receiptTask/' + getUser(),
    method: 'POST',
    data: {
      taskname
    }
  })
}


