<template>
  <div class="container">
    <BaseInput v-model="taskname">任务名</BaseInput>
    <button @click='nextStep'>下一步</button>
  </div>
</template>

<script>
import BaseInput from "@/components/BaseInput";
export default {
  name: "TaskName",
  components: {
    BaseInput
  },
  data: function() {
    return {
      taskname: ""
    };
  },
  methods: {
    nextStep: function() {
      this.$router.push("/createtask/" + this.taskname + "/imgPackage");
    }
  }
};
</script>



