<template>
  <div class="base-input">
    <div class="name">
      <slot/>:
      <span v-show="required"
        style="color:red;">*</span>
    </div>
    <input :type="type"
      :value="value"
      @input="$emit('input', $event.target.value)"
      @keyup="validate"
      @click="validate"
      :placeholder="tips">
    <div class="error-message"
      v-show="!isValid">× {{errorMessage}}</div>
  </div>
</template>

<script>
import { validate } from "../utils/validate";
export default {
  name: "Login",
  props: {
    value: [String, Number],
    type: {
      type: String,
      default: "text"
    },
    tips: {
      type: String,
      default: ""
    },
    required: {
      type: Boolean,
      default: true
    },
    rule: Object
  },
  data: function() {
    return {
      isValid: true
    };
  },
  computed: {
    errorMessage: function() {
      if (this.required && !this.value) {
        return "此处为必填项";
      } else {
        return "输入有误";
      }
    }
  },
  methods: {
    validate: function() {
      if (this.required || this.value) {
        this.isValid = validate(this.value, this.rule);
      } else {
        this.isValid = true;
      }
      return this.isValid;
    }
  }
};
</script>

<style scoped>
.base-input {
  text-align: left;
}
.name,
.error-message,
input {
  width: 100%;
}
.error-message {
  color: red;
  font-weight: bold;
  font-size: 12px;
}
</style>



