<template>
    <canvas ref="canvas" :width="imgWidth * resolution" :height="imgHeight * resolution"></canvas>
</template>

<script>
export default {
  name: "WorkCanvas",
  props: {
    info: Object,
    type: String,
    selected: Boolean,
    imgWidth: Number,
    imgHeight: Number,
    resolution: {
      type: Number,
      default: 4
    }
  },
  data: function() {
    return {
      workCanvas: null,
      workContext: null
    };
  },
  computed: {
    isLoaded: function() {
      return (
        this.workCanvas &&
        this.workCanvas.width > 0 &&
        this.workCanvas.height > 0
      );
    }
  },
  watch: {
    info: {
      handler: function(val) {
        if (val) {
          this.draw();
        } else {
          this.workCanvas.width = this.workCanvas.width;
        }
      },
      deep: true
    },
    isLoaded: function(val) {
      if (val) {
        this.draw();
      }
    },
    selected: function(val) {
      if (val) {
        this.fill();
      } else {
        this.draw();
      }
    }
  },
  mounted: function() {
    this.workCanvas = this.$refs.canvas;
    this.workContext = this.workCanvas.getContext("2d");
  },
  methods: {
    draw: function() {
      this.workCanvas.width = this.workCanvas.width;
      this.workContext.lineWidth = this.resolution / 2;
      this.workContext.lineJoin = "round";
      switch (this.type) {
        case "rectMark":
          this.rectDraw();
          break;
        case "polyMark":
          this.polyDraw();
          break;
      }
      this.workContext.stroke();
    },
    rectDraw: function() {
      this.workContext.rect(
        this.info.left * this.resolution,
        this.info.top * this.resolution,
        this.info.width * this.resolution,
        this.info.height * this.resolution
      );
    },
    polyDraw: function() {
      this.workContext.moveTo(
        this.info.x[0] * this.resolution,
        this.info.y[0] * this.resolution
      );
      for (var index in this.info.x) {
        this.workContext.lineTo(
          this.info.x[index] * this.resolution,
          this.info.y[index] * this.resolution
        );
      }
      this.workContext.closePath();
    },
    fill: function() {
      this.workContext.fillStyle = "rgba(0, 0, 0, 0.3)";
      this.workContext.fill();
    }
  }
};
</script>



