<template>
  <div class="description-item"
       :class="{selected:selected}">
    <div class="description-time">{{description.time}}</div>
    <DescriptionContent v-model="description.content"></DescriptionContent>
  </div>
</template>

<script>
import DescriptionContent from "./DescriptionListItemContent";
export default {
  name: "DescriptionItem",
  components: {
    DescriptionContent
  },
  props: {
    description: Object,
    selected: Boolean
  },
  computed: {
    content: function() {
      return this.description.content;
    }
  },
  watch: {
    content: function() {
      this.description.time = this.getTime();
    }
  },
  mounted: function() {
    if (!this.description.time) {
      this.description.time = this.getTime();
    }
  },
  methods: {
    getTime() {
      var date = new Date();
      function fix(num) {
        return (num < 10 ? "0" : "") + num;
      }
      return (
        date.getFullYear() +
        "/" +
        fix(date.getMonth()) +
        "/" +
        fix(date.getDate()) +
        " " +
        fix(date.getHours()) +
        ":" +
        fix(date.getMinutes())
      );
    }
  }
};
</script>

<style scoped>
.description-item {
  position: relative;
  border-bottom: 1px solid #000;
}
.description-item.selected {
  background: rgba(0, 0, 0, 0.1);
}
</style>



