<template>
  <div class="task-note-container">
    <div v-if="loading">Loading...</div>
    <TaskNote v-for="(task, index) in tasks"
      :key="index"
      :taskData="task" />
    <div v-if="!tasks||task == []">无任务</div>
  </div>
</template>

<script>
import TaskNote from "./TaskNote";
import { getAllTasks } from "@/api/base";
import { getReleasedTasks, getReceiptTasks } from "@/api/taskManagement";
export default {
  name: "TaskNoteContainer",
  components: {
    TaskNote
  },
  props: {
    role: String,
    username: String
  },
  data: function() {
    return {
      loading: false,
      tasks: []
    };
  },
  created: function() {
    this.fetchData();
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData: function() {
      this.loading = true;
      if (this.username) {
        if (this.role == "requester") {
          getReleasedTasks().then(response => {
            response = response.data;
            if (response && response.tasks) {
              this.tasks = response.tasks;
            } else {
              this.tasks = [];
            }
          });
        } else if (this.role == "worker") {
          getReceiptTasks().then(response => {
            response = response.data;
            if (response && response.tasks) {
              this.tasks = response.tasks;
            } else {
              this.tasks = [];
            }
          });
        }
      } else {
        getAllTasks().then(response => {
          response = response.data;
          if (response && response.tasks) {
            this.tasks = response.tasks;
          } else {
            this.tasks = [];
          }
        });
      }
      this.loading = false;
    }
  }
};
</script>



