<template>
  <WorkCanvas :info="info" :type="type" :imgWidth="width" :imgHeight="height"
    @click.native="click"
		@dblclick.native="dblclick"
		@mousemove.native="mousemove" />
</template>

<script>
import WorkCanvas from "./MarkAreaWorkCanvas";
export default {
  name: "DrawCanvas",
  components: {
    WorkCanvas
  },
  props: {
    type: String,
    scale: Number,
    width: Number,
    height: Number
  },
  data: function() {
    return {
      info: {},
      isDrawing: false
    };
  },
  methods: {
    click: function(e) {
      if (this[this.type + "Click"]) {
        this[this.type + "Click"](e);
      }
    },
    dblclick: function(e) {
      if (this[this.type + "Dblclick"]) {
        this[this.type + "Dblclick"](e);
      }
    },
    mousemove: function(e) {
      if (this[this.type + "Move"]) {
        this[this.type + "Move"](e);
      }
    },
    finishDraw: function() {
      this.isDrawing = false;
      this.$emit("finishDraw", this.info);
      this.info = null
    },
    rectMarkClick: function(e) {
      var mouseX = e.offsetX / this.scale;
      var mouseY = e.offsetY / this.scale;
      if (this.isDrawing) {
        this.info.width = mouseX - this.info.left;
        this.info.height = mouseY - this.info.top;
        this.finishDraw();
      } else {
        this.info = {
          left: 0,
          top: 0,
          width: 0,
          height: 0
        };
        this.info.left = mouseX;
        this.info.top = mouseY;
        this.isDrawing = true;
      }
    },
    rectMarkMove: function(e) {
      var mouseX = e.offsetX / this.scale;
      var mouseY = e.offsetY / this.scale;
      if (this.isDrawing) {
        this.info.width = mouseX - this.info.left;
        this.info.height = mouseY - this.info.top;
      }
    },
    polyMarkClick: function(e) {
      var mouseX = e.offsetX / this.scale;
      var mouseY = e.offsetY / this.scale;
      if (!this.isDrawing) {
        this.info = {
          x: [mouseX],
          y: [mouseY]
        };
        this.isDrawing = true;
      }
      this.info.x.push(mouseX);
      this.info.y.push(mouseY);
    },
    polyMarkDblclick: function(e) {
      for (var i = this.info.x.length - 1; i > 0; i--) {
        if (
          this.info.x[i] == this.info.x[i - 1] &&
          this.info.y[i] == this.info.y[i - 1]
        ) {
          this.info.x.splice(i, 1);
          this.info.y.splice(i, 1);
        }
      }
      this.finishDraw();
    },
    polyMarkMove: function(e) {
      var mouseX = e.offsetX / this.scale;
      var mouseY = e.offsetY / this.scale;
      if (this.isDrawing) {
        this.info.x.splice(this.info.x.length - 1, 1, mouseX);
        this.info.y.splice(this.info.y.length - 1, 1, mouseY);
      }
    }
  }
};
</script>



