<template>
  <div class="description-content"
       contenteditable=true
       @focus="isFocus = true"
       @blur="isFocus = false"
       @input="changeText"
       v-text="innerText"></div>
</template>

<script>
export default {
  name: "DescriptionContent",
  props: {
    value: String
  },
  data: function() {
    return {
      isFocus: false,
      innerText: this.value
    };
  },
  methods: {
    changeText: function() {
      this.$emit("input", this.$el.innerText);
    }
  },
  watch: {
    value: function() {
      if (!this.isFocus) {
        this.innerText = this.value;
      }
    }
  }
};
</script>

<style scoped>
.description-content {
  display: inline-block;
  word-break: keep-all;
  outline: none;
  border-radius: 7px;
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  -webkit-user-modify: read-write-plaintext-only;
}

.description-content:focus {
  white-space: pre-line;
  background: rgba(0, 0, 0, 0.1);
}
</style>


