<template>
  <div>
    <div class="img-area"
      :style="backgroundStyle"></div>
    <div>任务名：{{taskname}}</div>
    <div>任务发布者：{{taskDetail.taskRequestor}}</div>
    <div>任务开始时间：{{taskDetail.taskStartTime}}</div>
    <div>任务积分：{{taskDetail.points}}</div>
    <div>任务描述：{{taskDetail.taskDescription}}</div>
    <div>任务剩余时间：{{taskDetail.remainTime}}</div>
    <div>任务标签：
      <span v-for="(tag,index) in taskDetail.taskTag"
        :key="index">#{{tag}}</span>
    </div>
    <div>参与人数：{{taskDetail.currentWorker}}/{{taskDetail.maxWorker}}</div>
    <hr>
    <!-- <button v-if="isRequestor">修改任务信息</button> -->
    <button v-if="isRequestor"
      @click="deleteTask">结束任务</button>
    <button v-if="role == 'worker' && !isWorking && !isFinished"
      @click="receiptTask">接受任务</button>
    <button v-if="isFinished"
      disabled="true">√已完成</button>
    <button @click="$router.push('/mark/'+taskname)"
      v-if="isWorking">继续标记</button>
    <hr>
    <div v-for="(workerProcess, index) in taskDetail.currentWorkers"
      :key="index"
      v-if="isRequestor || workerProcess.workerName == username && role =='worker'">
      {{workerProcess.workerName}} {{workerProcess.process}}/{{taskDetail.numberOfImages}}
    </div>
  </div>
</template>

<script>
import { getTaskDetail } from "@/api/base";
import { getUser, getRole } from "@/utils/localstorageinfo";
import { deleteTask, receiptTask } from "@/api/taskManagement";
export default {
  name: "TaskDetail",
  props: {
    taskname: String
  },
  data: function() {
    return {
      taskDetail: {
        imgURL: null,
        taskname: null,
        taskRequestor: null,
        taskStartTime: null,
        points: null,
        taskDescription: null,
        remainTime: null,
        taskTag: null,
        currentWorker: null,
        maxWorker: null,
        numberOfImages: null,
        currentWorkers: null
      }
    };
  },
  computed: {
    username: function() {
      return getUser();
    },
    role: function() {
      return getRole();
    },
    isRequestor: function() {
      return (
        this.role == "requester" &&
        this.username == this.taskDetail.taskRequestor
      );
    },
    isWorking: function() {
      if (this.role != "worker") {
        return false;
      }
      for (var index in this.taskDetail.currentWorkers) {
        var temp = this.taskDetail.currentWorkers[index];
        if (temp.workerName == this.username) {
          if (temp.process < this.taskDetail.numberOfImages) {
            return true;
          } else {
            return false;
          }
        }
      }
      return false;
    },
    isFinished: function() {
      if (this.role != "worker") {
        return false;
      }
      for (var index in this.taskDetail.currentWorkers) {
        var temp = this.taskDetail.currentWorkers[index];
        if (temp.workerName == this.username) {
          if (temp.process == this.taskDetail.numberOfImages) {
            return true;
          } else {
            return false;
          }
        }
      }
      return false;
    },
    backgroundStyle: function() {
      if (this.taskDetail && this.taskDetail.imgURL) {
        return {
          background: "url('/" + this.taskDetail.imgURL + "') center center",
          backgroundSize: "contain",
          backgroundRepeat: "no-repeat"
        };
      }
    }
  },
  created: function() {
    this.fetchData();
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData: function() {
      getTaskDetail(this.taskname).then(response => {
        this.taskDetail = response.data;
      });
    },
    receiptTask: function() {
      receiptTask(this.taskname);
      this.$router.go(-1);
    },
    deleteTask: function() {
      deleteTask(this.taskname);
    }
  }
};
</script>

<style scoped>
.img-area {
  width: 320px;
  height: 180px;
}
</style>



