<template>
  <div class="container">
    <h1>登录</h1>
    <BaseInput v-model="username"
      tips="请输入用户名"
      :required="false">
      用户名
    </BaseInput>
    <BaseInput v-model="password"
      type="password"
      tips="请输入密码"
      :required="false">
      密码
    </BaseInput>
    <button @click='login'>登录</button>
  </div>
</template>

<script>
import BaseInput from "@/components/BaseInput";
import { login } from "@/api/base";
import { setUser, setRole, setToken } from "@/utils/localstorageinfo";

export default {
  name: "Login",
  components: {
    BaseInput
  },
  data: function() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    login: function() {
      login(this.username, this.password).then(response => {
        response = response.data;
        setUser(this.username);
        setToken(response.token);
        setRole(response.role);
        this.$router.push("/");
      });
    }
  }
};
</script>
<style scoped>
h1,
.base-input,
button {
  display: block;
  width: 100%;
  margin: 20px auto;
  max-width: 350px;
}
</style>



