import request from '@/request'
import { getUser } from '@/utils/localstorageinfo'

export function createPackage (taskname) {
  return request({
    url: '/service/images/createPackage/' + getUser(),
    method: 'POST',
    data: {
      taskname,
    },
  })
}

export function deletePackage (taskname) {
  return request({
    url: '/service/images/deletePackage/' + getUser(),
    method: 'POST',
    data: {
      taskname,
    },
  })
}


