<template>
  <div class="task-note"
    @click="onClick">
    <div class="img-area"
      :style="backgroundStyle"></div>
    <div class="task-name">{{taskData.taskname}}</div>
    <div class="clearfix">
      <div class="remain-time">{{time}}</div>
      <div class="worker-status">参与人数：{{taskData.currentWorker}}/{{taskData.maxWorker}}</div>
    </div>
    <div class="task-tag" v-if="taskData.taskTag">
      <span v-for="(tag,index) in taskData.taskTag"
        :key="index">#{{tag}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "TaskNote",
  props: {
    taskData: Object
  },
  computed: {
    time: function() {
      var t = this.taskData.remainTime;
      var days = parseInt(t / 60 / 24);
      var hours = parseInt((t / 60) % 24);
      var minutes = parseInt(t % 60);
      var result = "";
      if (t == 0) {
        return "任务已结束";
      }
      if (days) {
        result += days + "天";
      } else if (hours) {
        result += hours + "小时";
      } else if (minutes) {
        result += minutes + "分钟";
      }
      return "剩余时间：" + result;
    },
    backgroundStyle: function() {
      return {
        background: "url('/" + this.taskData.imgURL + "') center center",
        backgroundSize: "contain",
        backgroundRepeat: "no-repeat"
      };
    }
  },
  methods: {
    onClick: function() {
      this.$router.push("/tasks/" + this.taskData.taskname);
    }
  }
};
</script>
<style scoped>
.task-note {
  width: 250px;
  height: 200px;
  display: inline-block;
  border: 2px solid rgba(0, 0, 0, 0.5);
  text-align: left;
  padding: 10px 10px;
  margin: 20px 10px;
}
.img-area {
  width: 200px;
  height: 117px;
  margin: 0 auto;
}
.task-name {
  font-size: 24px;
  font-weight: bold;
}
.remain-time,
.task-tag,
.worker-status {
  font-size: 12px;
  margin-top: 7px;
}
.remain-time,
.worker-status {
  font-weight: 350;
  color: rgba(0, 0, 0, 0.5);
}
.task-tag > * {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin-right: 5px;
}
.remain-time {
  float: left;
}
.worker-status {
  float: right;
}
</style>



