<template>
  <div class="container">
    <BaseInput v-model="taskDescription"
      tips="请输入任务描述"
      :required=false>
      任务描述
    </BaseInput>
    <BaseInput v-model="credits"
      type="number"
      tips="请输入任务积分"
      :rule="{min:1}">
      任务积分
    </BaseInput>
    <BaseInput v-model="taskTime"
      type="number"
      tips="请输入任务时间"
      :rule="{min:1}">
      任务时间
    </BaseInput>
    <BaseInput v-model="taskTag"
      tips="请输入任务标签"
      :required=false>
      任务标签
    </BaseInput>
    <BaseInput v-model="maxWorkers"
      type="number"
      tips="请输入最大参与人数"
      :rule="{min:1}">
      最大参与人数
    </BaseInput>

    <button @click='submit'>提交</button>
  </div>
</template>

<script>
import BaseInput from "@/components/BaseInput";
import { addTask } from "@/api/taskManagement";
export default {
  name: "Signup",
  components: {
    BaseInput
  },
  props: {
    taskname: String
  },
  data: function() {
    return {
      taskDescription: "",
      credits: null,
      taskTime: null,
      taskTag: "",
      maxWorkers: null,
      valid: false
    };
  },
  methods: {
    submit: function() {
      this.validate();
      if (this.valid) {
        var data = {
          taskname: this.taskname,
          taskDescription: this.taskDescription,
          credits: this.credits,
          taskTime: this.taskTime,
          taskTag: this.taskTag.split(","),
          maxWorkers: this.maxWorkers
        };
        console.log(data);
        addTask(data).then(response => {
          this.$router.push("/tasks/" + this.taskname);
        });
      } else {
        alert("输入信息有误！");
      }
    },
    validate: function() {
      var inputs = this.$children;
      var index = 0;
      this.valid = true;
      while (index < inputs.length) {
        this.valid = inputs[index].validate() && this.valid;
        index++;
      }
    }
  }
};
</script>

<style scoped>
.base-input,
.role-wrap,
button {
  display: block;
  max-width: 350px;
}
.role-wrap {
  text-align: center;
}
.role-wrap > * {
  display: inline-block;
}



