import request from '@/request'
import { getUser } from '@/utils/localstorageinfo'

export function submitMark (data) {
  return request({
    url: '/service/mark/setMark/' + getUser(),
    method: 'POST',
    data,
  })
}

export function getNextImage (taskname) {
  return request({
    url: '/service/mark/findURL/' + getUser(),
    method: 'POST',
    data: {
      taskname,
    }
  })
}

export function findMark (taskname, imgID) {
  return request({
    url: '/service/mark/findMark/' + getUser(),
    method: 'POST',
    data: {
      taskname,
      imgID,
    }
  })
}

export function completeImage (taskname, imgID) {
  return request({
    url: '/service/mark/completeImg/' + getUser(),
    method: 'POST',
    data: {
      taskname,
      imgID,
    }
  })
}

export function getMark (taskname, imgID) {
  return request({
    url: '/service/mark/getMark/' + getUser(),
    method: 'POST',
    data: {
      taskname,
      imgID,
    }
  })
}

export function getImageURLs (taskname) {
  return request({
    url: '/service/mark/getURLS/' + getUser(),
    method: 'POST',
    data: {
      taskname,
    },
  })
}


