<template>
  <div class="mark-area"
    v-if="imgURL">
    <CanvasBoard :imgURL="imgURL"
      :imgWidth="width"
      :imgHeight="height">
      <template slot-scope="ss">
        <DrawCanvas :type="currentMarkMethod"
          :width="width"
          :height="height"
          :scale="ss.scale"
          :resolution="width > 1600 ? 2 : 3200 / width"
          style="z-index:1;"
          @finishDraw="finishDraw" />
        <template v-for="(item,type) in marks">
          <WorkCanvas v-for="(mark, index) in item"
            :key="type + index"
            :info="mark.info"
            :type="type"
            :selected="selected == type + '-' + index||hover == type + '-' + index"
            :imgWidth="width"
            :imgHeight="height"
            :resolution="width > 1600 ? 2 : 3200 / width" />
        </template>
      </template>
    </CanvasBoard>
    <DescriptionList>
      <template v-for="(item,type) in marks">
        <DescriptionItem v-for="(mark, index) in item"
          :key="type + index + 'description'"
          v-if="mark.description"
          :description="mark.description"
          :selected="selected == type + '-' + index||hover == type + '-' + index"
          @mouseover.native="hover = type + '-' + index"
          @mouseout.native="hover = null"
          @click.native="selected = type + '-' + index" />
      </template>
    </DescriptionList>
    <button @click="currentMarkMethod = 'rectMark'">rectMark</button>
    <button @click="currentMarkMethod = 'polyMark'">polyMark</button>
    <button @click="deleteMark">delete</button>
    <button @click="submit">submit</button>
    <button @click="complete">complete</button>
  </div>
</template>

<script>
import CanvasBoard from "./MarkAreaCanvasBoard";
import WorkCanvas from "./MarkAreaWorkCanvas";
import DrawCanvas from "./MarkAreaDrawCanvas";
import DescriptionList from "./DescriptionList";
import DescriptionItem from "./DescriptionListItem";
import { getNextImage, getMark, submitMark, completeImage } from "@/api/mark";
export default {
  name: "MarkArea",
  components: {
    CanvasBoard,
    DrawCanvas,
    WorkCanvas,
    DescriptionList,
    DescriptionItem
  },
  props: {
    taskname: String,
    imgID: String
  },
  data: function() {
    return {
      imgURL: null,
      width: 0,
      height: 0,
      marks: {
        wholeDesc: "",
        rectMark: [],
        polyMark: []
      },
      currentMarkMethod: null,
      selected: null,
      hover: null
    };
  },
  watch: {
    marks: {
      handler: function(val) {
        console.log(val);
      },
      deep: true
    },
    selected: function(val) {
      if (val) {
        this.currentMarkMethod = null;
      }
    },
    currentMarkMethod: function(val) {
      if (val) {
        this.selected = null;
      }
    }
  },
  created: function() {
    this.fetchData();
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData: function() {
      if (!this.taskname) {
        return;
      } else if (this.taskname && !this.imgID) {
        getNextImage(this.taskname)
          .then(response => {
            var imgURL = response.data.imgURL;
            var imgID = imgURL.split("/").pop();
            this.$router.push({
              path: "/mark/" + this.taskname + "/" + imgID,
              query: { imgURL: imgURL }
            });
          })
          .catch(error => {
            console.log(error);
            this.$router.push("/tasks/alltasks");
          });
      } else if (this.taskname && this.imgID) {
        this.getImg("/" + this.$route.query.imgURL);
      }
    },
    deleteMark: function() {
      if (this.selected) {
        var temp = this.selected.split("-");
        this.marks[temp[0]].splice(temp[1], 1);
        this.selected = null;
      }
    },
    finishDraw: function(data) {
      this.marks[this.currentMarkMethod].push({
        info: data,
        description: {
          time: "",
          content: ""
        }
      });
    },
    getImg: function(imgURL) {
      var me = this;
      var img = new Image();
      img.src = imgURL;
      var check = function() {
        if (img.width > 0 && img.height > 0) {
          me.width = img.width;
          me.height = img.height;
          me.imgURL = imgURL;
          clearInterval(set);
          me.getMarks();
        }
      };
      var set = setInterval(check, 40);
    },
    getMarks: function() {
      var me = this;
      getMark(this.taskname, this.imgID)
        .then(response => {
          if (response.data && response.data.aMark) {
            me.marks = response.data.aMark;
          }
        })
        .catch(error => {
          me.marks = {
            wholeDesc: "",
            rectMark: [],
            polyMark: []
          };
        });
    },
    submit: function() {
      submitMark({
        taskname: this.taskname,
        imgID: this.imgID,
        marks: this.marks
      });
    },
    complete: function() {
      completeImage(this.taskname, this.imgID).then(response => {
        this.$router.push("/mark/" + this.taskname);
      });
    }
  }
};
</script>

<style scoped>
.mark-area {
  width: 100%;
  height: 500px;
}
</style>



