<template>
  <div id="user-bar">
    <router-link to="/login"
      v-if="!username">登录</router-link>
    <div class="user-wrap"
      @mouseover="onHover = true"
      @mouseout="onHover = false">
      <router-link :to="'/user/' + username"
        tag="div"
        v-if="username">
        {{username}}
      </router-link>
      <transition name="fade">
        <div class="user-operation-wrap"
          v-if="onHover">
          <router-link :to="'/user/' + username + '/modify'"
            tag="label">修改信息</router-link>
          <label @click="logout">退出登录</label>
        </div>
      </transition>
    </div>
    <router-link to="/signup">注册</router-link>
  </div>
</template>

<script>
import { getUser } from "@/utils/localstorageinfo";
export default {
  name: "UserBar",
  data: function() {
    return {
      username: "",
      onHover: false
    };
  },
  mounted: function() {
    this.username = getUser();
  },
  methods: {
    logout: function() {
      localStorage.clear();
      this.$router.push("/blank");
    }
  }
};
</script>

<style scoped>
#user-bar {
  float: right;
  margin-right: 10px;
}
.user-wrap {
  display: inline-block;
}
.user-operation-wrap {
  position: absolute;
}
.user-operation-wrap > * {
  display: block;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}
.user-operation-wrap > *:hover {
  background-color: rgba(0, 0, 0, 0.2);
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>



