<template>
	<div class="description-list">
		<div class="description-time">时间</div>
		<div class="description-content">内容</div>
		<div class="description-item-wrap">
			<slot/>
		</div>
	</div>
</template>

<script>
export default {
  name: "DescriptionList"
};
</script>

<style>
.description-list {
  position: relative;
  box-sizing: content-box;
  border: 1px solid #000;
  height: 300px;
}
.description-time,
.description-content {
  padding: 2px 5px;
  margin: 2px;
  display: inline-block;
  text-align: center;
  height: 30px;
}
.description-time {
  width: 150px;
  border-right: 1px solid #000;
}
.description-content {
  position: absolute;
  left: 150px;
  right: 0px;
  top: 0px;
  bottom: 0px;
}
.description-item-wrap {
  width: 100%;
  position: absolute;
  top: 30px;
  bottom: 0px;
  overflow: auto;
}
</style>



