import request from '@/request'

export function login (username, password) {
  return request({
    url: '/base/login',
    method: 'POST',
    data: {
      username,
      password
    }
  })
}

export function signUp (username, password, email, role) {
  return request({
    url: '/base/signUp',
    method: 'POST',
    data: {
      username,
      password,
      email,
      role
    }
  })
}

export function getAllTasks () {
  return request({
    url: '/base/display/allTask',
    method: 'GET',
  })
}

export function getTaskDetail (taskname) {
  return request({
    url: '/base/display/detail/' + taskname,
    method: 'GET',
  })
}

export function uploadImage (taskname, imageObject) {
  return request({
    url: '/base/images/upload/' + taskname,
    method: 'POST',
    data: imageObject,
  })
}


