<template>
  <div class="container">
    <input type="file"
      multiple="multiple"
      @change="e=>files = e.target.files">
    <button @click='nextStep'>下一步</button>
  </div>
</template>

<script>
import { createPackage, deletePackage } from "@/api/images";
import { uploadImage } from "@/api/base";
export default {
  name: "ImgPackage",
  props: {
    taskname: String
  },
  data: function() {
    return {
      files: null,
      alreadyUpload: 0
    };
  },
  mounted: function() {
    createPackage(this.taskname).then(response => {
      if (response.code != 0) {
        this.$router.push(-1);
      }
    });
  },
  methods: {
    nextStep: function() {
      console.log(this.taskname);
      console.log(this.files);
      for (var index = 0; index < this.files.length; index++) {
        this.upload(index);
      }
    },
    upload: function(index) {
      var formData = new FormData();
      formData.append("imageObject", this.files[index]);
      uploadImage(this.taskname, formData).then(response => {
        if (response.code != 0) {
          if (confirm("上传图片" + index + "失败，是否重新上传?")) {
            this.upload(index);
          }
        } else {
          this.alreadyUpload++;
          if (this.alreadyUpload == this.files.length) {
            this.$router.push("/createtask/" + this.taskname + "/taskdetails");
          }
        }
      });
    }
  }
};
</script>



